body {
  margin: 0;
  padding: 0;
font-family: 'Roboto Condensed', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 background: -webkit-linear-gradient(45deg, rgba(255,99,239,1) 8%, rgba(230,103,240,1) 19%, rgba(46,140,255,1) 100%) no-repeat fixed;
 background: linear-gradient(45deg, rgba(255,99,239,1) 8%, rgba(230,103,240,1) 19%, rgba(46,140,255,1) 100%) no-repeat fixed; /* w3c */

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#logo{text-align: left;}
.App {
  text-align: center;
}
.center{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.particles{
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right:0;
  z-index: -1;
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Tilt{
	 background: -webkit-linear-gradient(45deg, rgba(255,99,239,1) 8%, rgba(230,103,240,1) 19%, rgba(46,140,255,1) 100%)  no-repeat;
	 background: linear-gradient(45deg, rgba(255,99,239,1) 8%, rgba(230,103,240,1) 19%, rgba(46,140,255,1) 100%)  no-repeat; /* w3c */
	
}


